





















import YgoCard from "./YgoCard.vue";
import YgoPriceView from "./YgoPriceView.vue";
import { Card, Deck, DeckPart } from "../../../core/src/main";
import Component from "vue-class-component";
import Vue from "vue";
import { Prop } from "vue-property-decorator";

@Component({
    components: {
        YgoCard,
        YgoPriceView,
    },
})
export default class YgoDeckPart extends Vue {
    @Prop({ required: true })
    deck: Deck;
    @Prop({ required: true })
    deckPart: DeckPart;

    get cards() {
        return this.deck.parts.get(this.deckPart);
    }

    onDeckCardRightClicked(e: any, card: Card) {
        this.$emit("deck-card-right-click", e, card);
    }
}
