












import Component from "vue-class-component";
import Vue from "vue";
import { Model, Prop } from "vue-property-decorator";

@Component({})
export default class AdvancedSelect<T> extends Vue {
    @Model("input")
    initialValue: T | null;

    @Prop({ required: true })
    initialOptions: T[];

    @Prop({ required: false, default: false })
    noSelectionAllowed: boolean;

    @Prop({ required: false, default: null })
    title: string | null;

    @Prop({ required: false, default: (str: string): string => str })
    label: (T) => string;

    @Prop({ required: false, default: (key: string): string => key })
    trackBy: (T) => string;

    value: T | null;

    get options(): Array<T | null> {
        return this.noSelectionAllowed
            ? [null, ...this.initialOptions]
            : this.initialOptions;
    }

    data() {
        return {
            value: this.initialValue,
        };
    }

    onChange() {
        this.$emit("input", this.value);
    }
}
